import { User } from "../model/user";
import { Constants } from "./constants";

export class Shared {
    constructor() {}
  
    /**
      Cadastra um usurio default para funcionamento do login.
      S realiza o cadastro caso o usurio ainda no esteja salvo no WebStorage.
    */
    public static initializeWebStorage(): void {
      if (localStorage.getItem(Constants.USERNAME_KEY) != null) {
        return;
      }
  
      //usurio definido na forma literal
      let user = new User(Constants.USERNAME_KEY, 'demo', true);
  
      localStorage.setItem(Constants.USERNAME_KEY, JSON.stringify(user));
      localStorage.setItem(Constants.USERS_KEY, JSON.stringify([]));
      localStorage.setItem(Constants.LOGGED_IN_KEY, String(false));
    }
  }